<template>
  <view class="uni-container">
    <uni-forms ref="form" :model="formData" validateTrigger="bind">
      <uni-forms-item name="avatarUrl" label="头像">
        <uni-file-picker file-mediatype="image" file-extname="jpg,png" return-type="object" v-model="formData.avatarUrl"></uni-file-picker>
      </uni-forms-item>
      <uni-forms-item name="username" label="姓名" required>
        <uni-easyinput placeholder="请填写" v-model="formData.username" trim="both"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="phone" label="手机号" required>
        <uni-easyinput placeholder="请输入" v-model="formData.phone" trim="both"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="gender" label="性别">
        <uni-data-checkbox v-model="formData.gender" :localdata="formOptions.gender_localdata"></uni-data-checkbox>
      </uni-forms-item>
      <uni-forms-item name="age" label="年龄">
        <uni-easyinput placeholder="请输入年龄" v-model="formData.age" trim="both"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="balance" label="余额">
        <uni-easyinput placeholder="请输入余额" type="number" v-model="formData.balance"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="merchantID" label="所属商家">
        <uni-data-picker v-model="formData.merchantID" collection="merchant" field="merchantName as text,_id as value" trim="both"></uni-data-picker>
      </uni-forms-item>
      <uni-forms-item name="contactAddress" label="联系地址">
        <uni-easyinput placeholder="联系地址" v-model="formData.contactAddress" trim="both"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="residentAddress" label="常驻地址">
        <uni-easyinput placeholder="常驻地址" v-model="formData.residentAddress" trim="both"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="latitude" label="纬度">
        <uni-easyinput placeholder="纬度：24.885889" type="number" v-model="formData.latitude"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="longitude" label="经度">
        <uni-easyinput placeholder="经度：118.598228" type="number" v-model="formData.longitude"></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="level " label="等级">
        <uni-easyinput placeholder="请输入" type="number" v-model="formData.level "></uni-easyinput>
      </uni-forms-item>
      <uni-forms-item name="workPhoto" label="工作照">
        <uni-file-picker file-mediatype="image" file-extname="jpg,png" return-type="object" v-model="formData.workPhoto"></uni-file-picker>
      </uni-forms-item>
      <uni-forms-item name="lifePhoto" label="生活照">
        <uni-file-picker file-mediatype="image" file-extname="jpg,png" return-type="object" v-model="formData.lifePhoto"></uni-file-picker>
      </uni-forms-item>
      <uni-forms-item name="IDFront" label="身份证正面">
        <uni-file-picker file-mediatype="image" file-extname="jpg,png,gif,bmp,jpeg" return-type="object" v-model="formData.IDFront"></uni-file-picker>
      </uni-forms-item>
      <uni-forms-item name="IDReverse" label="身份证反面">
        <uni-file-picker file-mediatype="image" file-extname="jpg,png,gif,bmp,jpeg" return-type="object" v-model="formData.IDReverse"></uni-file-picker>
      </uni-forms-item>
      <uni-forms-item name="skillCertificate" label="技能证书">
        <uni-file-picker file-mediatype="image" file-extname="jpg,png,gif,bmp,jpeg" :limit="10" return-type="array" v-model="formData.skillCertificate"></uni-file-picker>
      </uni-forms-item>
      <uni-forms-item name="classType" label="所属分类">
        <uni-data-picker v-model="formData.classType" collection="classType" field="classTypeName as text,_id as value" trim="both"></uni-data-picker>
      </uni-forms-item>
      <uni-forms-item name="auditStatus" label="审核状态">
        <uni-data-checkbox v-model="formData.auditStatus" :localdata="formOptions.auditStatus_localdata"></uni-data-checkbox>
      </uni-forms-item>
      <uni-forms-item name="isEnable" label="生效状态">
        <switch @change="binddata('isEnable', $event.detail.value)" :checked="formData.isEnable"></switch>
      </uni-forms-item>
      <view class="uni-button-group">
        <button type="primary" class="uni-button" style="width: 100px;" @click="submit">提交</button>
        <navigator open-type="navigateBack" style="margin-left: 15px;">
          <button class="uni-button" style="width: 100px;">返回</button>
        </navigator>
      </view>
    </uni-forms>
  </view>
</template>

<script>
  import { validator } from '../../js_sdk/validator/worker.js';

  const db = uniCloud.database();
  const dbCmd = db.command;
  const dbCollectionName = 'worker';

  function getValidator(fields) {
    let result = {}
    for (let key in validator) {
      if (fields.includes(key)) {
        result[key] = validator[key]
      }
    }
    return result
  }

  

  export default {
    data() {
      let formData = {
        "avatarUrl": null,
        "username": "",
        "phone": "",
        "gender": 1,
        "age": "",
        "balance": null,
        "merchantID": "67723526fe975f6f96069a43",
        "contactAddress": "",
        "residentAddress": "",
        "latitude": null,
        "longitude": null,
        "level ": null,
        "workPhoto": null,
        "lifePhoto": null,
        "IDFront": null,
        "IDReverse": null,
        "skillCertificate": [],
        "classType": "",
        "auditStatus": 1,
        "isEnable": true
      }
      return {
        formData,
        formOptions: {
          "gender_localdata": [
            {
              "text": "男",
              "value": 1
            },
            {
              "text": "女",
              "value": 2
            }
          ],
          "auditStatus_localdata": [
            {
              "text": "审核中",
              "value": 1
            },
            {
              "text": "通过",
              "value": 2
            },
            {
              "text": "不通过",
              "value": 3
            }
          ]
        },
        rules: {
          ...getValidator(Object.keys(formData))
        }
      }
    },
    onLoad(e) {
      if (e.id) {
        const id = e.id
        this.formDataId = id
        this.getDetail(id)
      }
    },
    onReady() {
      this.$refs.form.setRules(this.rules)
    },
    methods: {
      
      /**
       * 验证表单并提交
       */
      submit() {
        uni.showLoading({
          mask: true
        })
        this.$refs.form.validate().then((res) => {
          return this.submitForm(res)
        }).catch(() => {
        }).finally(() => {
          uni.hideLoading()
        })
      },

      /**
       * 提交表单
       */
      submitForm(value) {
        // 使用 clientDB 提交数据
        return db.collection(dbCollectionName).doc(this.formDataId).update(value).then((res) => {
          uni.showToast({
            title: '修改成功'
          })
          this.getOpenerEventChannel().emit('refreshData')
          setTimeout(() => uni.navigateBack(), 500)
        }).catch((err) => {
          uni.showModal({
            content: err.message || '请求服务失败',
            showCancel: false
          })
        })
      },

      /**
       * 获取表单数据
       * @param {Object} id
       */
      getDetail(id) {
        uni.showLoading({
          mask: true
        })
        db.collection(dbCollectionName).doc(id).field("avatarUrl,username,phone,gender,age,balance,merchantID,contactAddress,residentAddress,latitude,longitude,level ,workPhoto,lifePhoto,IDFront,IDReverse,skillCertificate,classType,auditStatus,isEnable").get().then((res) => {
          const data = res.result.data[0]
          if (data) {
            this.formData = data
            
          }
        }).catch((err) => {
          uni.showModal({
            content: err.message || '请求服务失败',
            showCancel: false
          })
        }).finally(() => {
          uni.hideLoading()
        })
      }
    }
  }
</script>
